import Test1 from "./test1";
import { BoardHeader } from '@/components';
import { GetStatusById } from '@/api/equipmentManagement/status'; // 数据接口

export default {
  name: 'BoardData',
  components: {
    BoardHeader: BoardHeader,
    Test1: Test1
  },
  data: function data() {
    return {
      viewTheme: undefined,
      position: [],
      headValue: {
        activeHome: false,
        detailBtn: '设备详情'
      },
      data: '',
      connected: '',
      timer: null
    };
  },
  created: function created() {
    var _this = this;

    this.viewTheme = this.$store.state.user.config.viewTheme;
    this.getData();
    this.timer = setInterval(function () {
      _this.getData();
    }, 7000);
    var template = JSON.parse(this.$store.state.user.config.boardDataTemplate);
    this.$set(this, 'position', template);
  },
  beforeDestroy: function beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    getData: function getData() {
      var _this2 = this;

      GetStatusById(this.$route.query.id).then(function (res) {
        _this2.data = res.data.statusData;
        _this2.connected = res.data.connected;
      });
    }
  }
};